import { parseDocument } from "htmlparser2";
import { DomUtils } from "htmlparser2";
import { Step } from "../types/user";
export  async function xmlToString(xml: string) {
    const cleanedXml = xml.replace(/^```xml\s*/, "").replace(/\s*```$/i, "");
    const doc = parseDocument(cleanedXml);
    const actions = DomUtils.findAll((el) => el.name === "boltaction", doc.children);

    const allSteps: Step[] = actions.map((el) => ({
      title: el.attribs.filepath,
      status: "Pending",
      content: DomUtils.getText(el), // preserves inner HTML/text
    }));;
    return allSteps
  }